
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "home",
components: {
Tabbar,
TabbarItem
},
data(){
return {
homeStyle: ''

}
},
mounted() {
const self = this
this.homeStyle = window.document.body.offsetHeight - this.$refs.tab.$el.offsetHeight + 'px'
window.onresize = () => {
self.homeStyle = window.document.body.offsetHeight - self.$refs.tab.$el.offsetHeight + 'px'
}
}
}
